import { Component, EventEmitter, Input, OnInit, Output, ChangeDetectorRef, ViewChild } from '@angular/core';
import { IonSlides } from '@ionic/angular';

@Component({
    selector: 'add-valor-component',
    templateUrl: 'add-valor-component.html',
    styleUrls: ['./add-valor-component.scss']
})

export class AddValorComponent implements OnInit {

    @Output() onChangeValue = new EventEmitter();

    @Input() classCSS: string;
    @Input() defaultValue: any;

    listaValores: any[] = [20, 50, 100, 120];

    valorSelecionado: number;

    constructor() {

    }

    ngOnInit() {
        console.log('ngOnInit -> AddValorComponent');
        this.valorSelecionado = this.defaultValue;
    }

    focusInput(evt) {
        if (this.valorSelecionado >= 1) {
            this.valorSelecionado = null;
        }
    }

    changeInput(evt) {
        if (evt.detail && evt.detail.value) {
            this.valorSelecionado = evt.detail.value;
        }

        this.emitChangeValue();
    }

    addValor(value: any) {
        if (value) {
            this.valorSelecionado = value;
        } else {
            this.valorSelecionado = Number(this.valorSelecionado) + 5;
        }

        this.emitChangeValue();
    }

    delValor() {
        if (this.valorSelecionado >= 5) {
            this.valorSelecionado = Number(this.valorSelecionado) - 5;
        }
        this.emitChangeValue();
    }

    emitChangeValue() {
        this.onChangeValue.emit({
            data: this.valorSelecionado
        });
    }
}
