import { NgModule } from '@angular/core';
import { PreloadAllModules, RouterModule, Routes } from '@angular/router';
import { SobrePage } from './sobre/sobre.page';

// @ts-ignore
const routes: Routes = [
    // {
    //     path: '',
    //     redirectTo: 'folder/Inbox',
    //     pathMatch: 'full'
    // },
    {
        path: '',
        redirectTo: 'onboard',
        pathMatch: 'full'
    },
    {
        path: 'onboard',
        loadChildren: () => import('./onboard/onboard.module').then(m => m.OnboardPageModule)
    },
    {
        path: 'login',
        loadChildren: () => import('./login/login.module').then(m => m.LoginPageModule)
    },
    {
        path: 'criar-conta',
        loadChildren: () => import('./criar-conta/criar-conta.module').then(m => m.CriarContaPageModule)
    },
    {
        path: 'home',
        loadChildren: () => import('./home/home.module').then(m => m.HomePageModule)
    },
    {
        path: 'transferir',
        loadChildren: () => import('./transferir/transferir.module').then(m => m.TransferirPageModule)
    },
    {
        path: 'central-duvidas',
        loadChildren: () => import('./central-duvidas/central-duvidas.module').then(m => m.CentralDuvidasPageModule)
    },
    {
        path: 'aprender-jogar',
        loadChildren: () => import('./aprender-jogar/aprender-jogar.module').then(m => m.AprenderJogarPageModule)
    },
    {
        path: 'termo-uso',
        loadChildren: () => import('./termo-uso/termo-uso.module').then(m => m.TermoUsoPageModule)
    },
    {
        path: 'sobre',
        loadChildren: () => import('./sobre/sobre.module').then(m => m.SobrePageModule)
    },
    {
        path: 'jogo',
        loadChildren: () => import('./jogo/jogo.module').then(m => m.JogoPageModule)
    },
    {
        path: 'folder/:id',
        loadChildren: () => import('./folder/folder.module').then(m => m.FolderPageModule)
    }
];

@NgModule({
    imports: [
        RouterModule.forRoot(routes, {preloadingStrategy: PreloadAllModules})
    ],
    exports: [RouterModule]
})
export class AppRoutingModule {
}
