import { Component, OnInit } from '@angular/core';
import { ModalController } from '@ionic/angular';
import * as moment from 'moment';

@Component({
    selector: 'modal-selecionar-dia',
    templateUrl: 'modal-selecionar-dia-component.html',
    styleUrls: ['modal-selecionar-dia-component.scss']
})

export class ModalSelecionarDiaComponent implements OnInit {

    listType: any[] = [
        '10',
        '14',
        '16',
        '18',
        '20',
    ];

    dia: any;
    sorteios: any[] = [];

    constructor(private modalCtrl: ModalController) {
    }

    async ngOnInit() {
        console.log('ngOnInit - > ModalSelecionarDiaComponent');
    }

    onSelectType(hora) {
        if (this.sorteios.indexOf(hora) <= -1) {
            this.sorteios.push(hora);
        } else {
            const ind = this.sorteios.indexOf(hora);
            this.sorteios.splice(ind, 1);
        }
    }

    onConfirm() {
        // moment(this.dia).format('LL');   // 16 de Março de 2020
        this.modalCtrl.dismiss({
            dia: moment(this.dia).format('DD/MM/YYYY'),
            sorteios: this.sorteios
        });
    }

    onClose() {
        this.modalCtrl.dismiss();
    }
}
